import React, { createContext, useContext, useState } from 'react'

const AEPSContext = createContext()


const AEPSContextProvider = ({walletCall , children}) => {

    const [allAEPSProvider , setAllAEPSProvider] = useState([])
        const [selectedAEPSProvider , setSelectedAEPSProvider]  = useState()
    const [aepsglobalLoader , setAepsGlobalLoader] = useState(false)
   
      const [aepscurrentPage , setAepsCurrentPage] = useState('')
      const [dailyAuth , setDailyAuth] = useState(false)
      const [onBoardingAuth , setOnBoardingAuth] = useState(false)
      const [triggerAuthApi , setTriggerAuthApi] = useState(false)
  return (
     <AEPSContext.Provider value={{setTriggerAuthApi ,triggerAuthApi,walletCall, setDailyAuth, setOnBoardingAuth, setSelectedAEPSProvider,allAEPSProvider ,setAllAEPSProvider,selectedAEPSProvider,aepsglobalLoader,setAepsGlobalLoader,aepscurrentPage , setAepsCurrentPage}}>
         {children}
     </AEPSContext.Provider>
  )
}

export const  useAEPSService = ()=> useContext(AEPSContext) 

export default AEPSContextProvider