import React, { useState, useRef } from "react";
import {
  Dialog,
  DialogTitle,
  DialogActions,
  TextField,
  Button,
  Box,
} from "@mui/material";
import axios from "axios";
import { baseUrl } from "../baseUrl";
import toastMessage from "./toastMessage";
import { getLocation } from "./getLocation";

const CustomTextField = (props) => (
  <TextField
    variant="outlined"
    size="small"
    fullWidth
    {...props}
  />
);

const withPinLocation = (Wrapped) => {
  return function PinLocationWrapper(props) {
    const [open, setOpen] = useState(false);
    const [pin, setPin] = useState("");
    const [error, setError] = useState("");
    const [loading, setLoading] = useState(false);

    const pendingResolvers = useRef([]);

    const ensureLocation = () =>
      new Promise((resolve, reject) => {
        pendingResolvers.current.push({ resolve, reject });
        setOpen(true);
      });

    const submitPin = async () => {
      if (!/^\d{4}$/.test(pin)) {
        setError("Enter a valid 4-digit PIN");
        return;
      }

      setLoading(true);
      try {
        const { latitude, longitude } = await getLocation();

        const body = {
          pin,
          latitude,
          longitude,
        };

        const res = await axios.post(
          `${baseUrl}user/verify-mobile-location-pin`,
          body,
          {
            headers: {
              "Content-Type": "application/json",
              Authorization: `Bearer ${localStorage.getItem("userToken")}`,
            },
          }
        );

        const fetched = res?.data;

        if (fetched?.statusCode === 200) {
          pendingResolvers.current.forEach(({ resolve }) =>
            resolve(fetched.data)
          );
          pendingResolvers.current = [];

          setOpen(false);
          setPin("");
        }

        toastMessage(fetched?.error, fetched?.message);
        setError("");
      } catch (err) {
        if (err?.code === 1) {
          setError("Please enable location to continue");
        } else {
          setError("PIN not recognised or service unavailable");
        }
      } finally {
        setLoading(false);
      }
    };

    const handleClose = () => {
      pendingResolvers.current = [];
      setOpen(false);
      setPin("");
      setError("");
    };

    return (
      <Box>
        <Wrapped {...props} ensureLocation={ensureLocation} />

        <Dialog open={open} onClose={handleClose} fullWidth maxWidth="xs">
          <DialogTitle align="center">
            Enter the 4-digit TPIN
          </DialogTitle>

          <Box px={3}>
            <CustomTextField
              label="Enter TPIN"
              value={pin}
              autoFocus
              onChange={(e) =>
                setPin(e.target.value.replace(/\D/g, "").slice(0, 4))
              }
              error={!!error}
              helperText={error}
            />
          </Box>

          <DialogActions sx={{ justifyContent: "center" }}>
            <Button
              disabled={pin.length !== 4 || loading}
              variant="contained"
              onClick={submitPin}
            >
              Submit
            </Button>
          </DialogActions>
        </Dialog>
      </Box>
    );
  };
};

export default withPinLocation;